.friend_list {
  width: 400px;
  max-width: 100%;
  display: flex;
  flex-direction: column;
  padding: 0;
  justify-content: center;
  margin-bottom: 50px;
}
.item {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  border-radius: 2px;
  width: 100%;
  display: flex;
  flex-direction: row;
  padding: 10px;
  align-items: center;

  background-color: #fff;

  &:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.4);
  }

  & + & {
    margin-top: 10px;
  }
}

.name {
  color: #2e2e2e;
  font-size: 30px;
  font-weight: 500;
  margin-left: 10px;
}
.status {
  &_online {
    background-color: #4fae52;
  }
  &_offline {
    background-color: #ff524d;
  }
  &_online,
  &_offline {
    border-radius: 50%;
    width: 10px;
    height: 10px;
    margin-right: 10px;
  }
}
