.profile {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  border-radius: 5px;
  padding-top: 30px;
  width: 330px;
  max-width: 100%;
  text-align: center;

  background-color: #fff;

  & p + p {
    margin-top: 10px;
  }

  &:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  }
}

.avatar {
  border-radius: 50%;
  width: 150px;
  max-width: 100%;
  margin-bottom: 30px;
}

.stats {
  display: flex;
  flex-direction: row;
  padding: 0;
  justify-content: center;
  background-color: #f3f6f9;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;

  & li {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 30px 0;
    width: calc(100% / 3);
    border-top: 1px solid #e1e7ef;
  }

  & li + li {
    border-left: 1px solid #e1e7ef;
  }
}

.name,
.quantity {
  color: #394454;
  font-weight: 700;
}

.name {
  font-size: 24px;
}

.location,
.tag,
.label {
  color: #a0abb6;
  font-weight: 500;
}

.label {
  margin-bottom: 5px;
}

.description {
  padding: 10px;
  margin-bottom: 30px;
}
