.statistics {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  border-radius: 2px;
  width: 500px;
  max-width: 100%;
  text-align: center;

  background-color: #fff;

  &:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  }

  margin: 50px 0;
}

.stat_list {
  display: flex;
  flex-direction: row;
  padding: 0;
  justify-content: center;
  background-color: #f3f6f9;
  border-radius: 2px;

  & .item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 30px 0;
    width: calc(100%);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    &:hover {
      box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.5);
    }
  }
  & .item:nth-child(n) {
    background-color: #50c4f4;
  }
  & .item:nth-child(2n) {
    background-color: #a43cf3;
  }
  & .item:nth-child(3n) {
    background-color: #e64c66;
  }
  & .item:nth-child(4n) {
    background-color: #20b8c5;
  }
}

.title {
  padding: 40px;
  text-transform: uppercase;
  font-size: 24px;
  color: #5c6368;
}
.label,
.percentage {
  color: #fff;
  font-size: 15px;
}

.percentage {
  color: #fff;
  margin-top: 5px;
  font-size: 26px;
}
